import React from 'react'
import Link from 'gatsby-link'
import Talk from '../images/meeting.svg'
import Chemistry from '../images/chemistry.svg'
import Idea from '../images/idea.svg'
const About = () => (
  <div className="main-wrapper animated fadeIn">
    <h1>
      lets talk
      <span className="title-end">_</span>
    </h1>
    <div className="contact">
      <div className="contact-image">
        <img src={Talk} alt="Phanor Coll" />
      </div>
      <div className="subtitle content">
        want to discuss an idea,
        <img src={Idea} alt="Phanor Coll" /> have a brain storming session or
        just get down to business and bring them to life, just drop me a line.
      </div>
      <div className="contact-email">phanorcoll@gmail.com</div>
      <div className="subtitle content">
      checkout
      <img src={Chemistry} alt="Phanor Coll" />
      <Link className="a-animation standout" to="/lab">
        my lab
      </Link>{' '}
      to see my latest projects, both personal and work.
    </div>
    </div>
  </div>
)

export default About



